.event{
    width: 100%;
    min-height: 100%;
    background-color: $blueBg;
    &__inner{
        display: flex;
        flex-direction: column;
        padding: 55px 79px 0px 37px;
    }
    &__content{
        margin-top: 28px;
        margin-left: -17px;
        margin-right: -17px;
        display: flex;
        // flex-wrap: wrap;
        align-items: flex-start;
    }
    &__column{
        max-width: 541px;
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    &__item{
        margin: 17px;
        max-width: 524px;
        min-width: 380px;
        width: 100%;
        background-color: $blueCardBg;
        border-radius: 18px;
        &-container{
            position: relative;
            padding: 27px 20px;
            overflow: hidden;
            & + .event__item-container{
                border-top: 2px solid #151E41;
            }
        }
    }
}

.structure{
    &__title{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        position: relative;
        z-index: 10;
        background-color: $blueCardBg;
        &--inner{
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
        }
    }
    &__name{
        font-weight: 700;
        font-size: 14px;
        line-height: 150%;
        color: $white;
    }
    &__status{
        font-weight: 300;
        font-size: 12px;
        line-height: 150%;
        color: $pink;
    }
    &__btn{
        flex: 0 0 auto;
        position: relative;
        width: 15px;
        height: 15px;
        border: none;
        background-color: transparent;
        cursor: pointer;
        &-line{
            height: 1px;
            width: 8px;
            background-color: $white;
            transition: transform .3s ease 0s;
            &:first-child{
                position: absolute;
                top: 6px;
                left: 1px;
                transform: rotateZ(135deg);
            }
            &:last-child{
                position: absolute;
                top: 6px;
                right: 1px;
                transform: rotateZ(45deg);
            }
        }
        &.close{
            & > .structure__btn-line{
                &:first-child{
                    transform: rotateZ(45deg);
                }
                &:last-child{
                    transform: rotateZ(135deg);
                }
            }
        }
    }
    &__body{
        display: flex;
        flex-direction: column;
        opacity: 1;
        position: static;
        transition: transform .4s ease 0s;
        // transition: opacity .1s ease-in 0s;
        &.show{
            transform: translateY(-300px);
            overflow: hidden;
            opacity: 0;
            position: absolute;
        }
    }
    &__text{
        margin-top: 25px;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: $white;
    }
    &__count-worker{
        margin-top: 10px;
    }
    &__icon{
        margin-bottom: -3px;
        margin-right: 7px;
        width: 17px;
        height: 17px;
        fill: $white;
    }
    &__rename{
        margin-top: 11px;
    }
}

.documents{
    &__item{
        margin-bottom: 10px;
        display: flex;
        align-items: center;
    }
    &__name{
        font-weight: 600;
        font-size: 14px;
        color: $white;
        margin-right: 11px;
    }
    &__line{
        height: 12px;
        width: 100%;
        border-bottom: 1px solid $white;
    }
    &__btn{
        margin-left: 11px;
        border-radius: 6px;
        border: 1px solid transparent;
        background-color: $darkBlue;
        padding: 7px 30px;
        font-weight: 500;
        font-size: 16px;
        color: $lightBlue;
        cursor: pointer;
        transition: border .3s ease 0s;
        &:hover{
            border-color: $lightBlue;
        }
    }
}

.check{
    &__point{
        display: flex;
        flex-direction: column;
        & + .check__point{
            margin-top: 17px;
        }
    }
    &__name{
        margin-bottom: 6px;
        font-weight: 400;
        font-size: 14px;
        color: $white;
    }
    &__inp{
        padding: 7px 10px;
        background-color: #3E4459;
        border-radius: 5px;
        border: 1px solid transparent;
        font-weight: 300;
        font-size: 14px;
        line-height: 150%;
        color: #C4C4C4;
        transition: border, color .3s ease 0s;
        &:focus{
            outline: none;
            border-color: $lightBlue;
            color: $white;
        }
        &#decision{
            min-height: 56px;
            resize: none;
        }
        &-select{
            max-width: 303px;
            cursor: pointer;
        }
    }
    &__btn{
        margin: 0px auto;
        border-radius: 6px;
        border: 1px solid transparent;
        background-color: $darkBlue;
        padding: 7px 30px;
        font-weight: 500;
        font-size: 16px;
        color: $lightBlue;
        cursor: pointer;
        transition: border .3s ease 0s;
        &:hover{
            border-color: $lightBlue;
        }
    }
}
.date{
    &__inps{
        width: min-content;
        padding: 7px 10px;
        border-radius: 5px;
        background-color: #3E4459;
        display: flex;
        font-weight: 300;
        color: #C4C4C4;
    }
    &__inp{
        background-color: transparent;
        border: none;
        outline: none;
        font-weight: 300;
        font-size: 14px;
        color: #C4C4C4;
        &::-webkit-clear-button{
            display: none;
        }
        &::-webkit-calendar-picker-indicator{
            display: none;
        }
        &::-webkit-calendar-picker-indicator {
            display: none;
        }
    }
}

@media (max-width: (1100px)) {
    .event__inner{
        align-items: center;
    }
    .event__content{
        flex-direction: column;
    }
}
@media (max-width: (650px)) {
    .event{
        margin-left: 35px;
    }
    .event__inner{
        padding: 10px;
    }
    .event__content{
        flex-direction: column;
        margin-left: 5px;
        margin-right: 5px;
    }
    .event__item{
        margin: 0 0 10px 0;
    }
    .event__item-container{
        padding: 5px 10px;
    }
}