.home{
    width: 100%;
    height: 100%;
    background-color: $blueBg;
    &__inner{
        margin: 0 auto;
        max-width: 1100px;
        padding: 55px 10px 0px 10px;
    }
    &__content{
        margin-top: 34px;
        margin-left: -15px;
        margin-right: -15px;
        display: flex;
        flex-wrap: wrap;
    }
    &__item{
        width: 100%;
        min-height: 230px;
        padding: 20px;
        border-radius: 18px;
        background-color: $blueCardBg;
        box-shadow: 7px 7px 8px rgba(0, 0, 0, 0.15);
        margin: 15px;
        &-s{
            max-width: 340px;
        }
        &-l{
            max-width: 710px;
        }
        &-m{
            max-width: 525px;
            min-height: 270px;
        }
    }
    &__title{
        font-weight: 600;
        font-size: 16px;
        line-height: 150%;
        color: $white;
    }
}

.work{
    &__logo{
        display: flex;
        justify-content: flex-end;
        & > img{
            max-width: 186px;
            height: 148px;
        }
    }
}
.stat{
    &__title{
        position: relative;
    }
    &__subtitle{
        position: absolute;
        top: 30px;
        left: 0;
        font-weight: 400;
        font-size: 14;
    }
    &__map{
        display: flex;
        justify-content: flex-end;
        & > img{
            max-width: 274px;
            max-height: 146px;
        }
    }
}
.weather{
    &__text{
        display: flex;
        flex-direction: column;
    }
    &__row{
        display: flex;
        align-items: flex-end;
        &:first-child{
            margin-bottom: 30px;
        }
        &:last-child{
            font-weight: 400;
            font-size: 14px;
            line-height: 150%;
            color: $lightGray;
        }
    }
    &__column{
        &.date{
            max-width: 100px;
            font-weight: 400;
            font-size: 14px;
            line-height: 120%;
            color: $white;
        }
        &.degrees{
            font-weight: 300;
            font-size: 80px;
            color: $white;
            position: relative;
            &::after{
                position: absolute;
                top: 0;
                right: -30px;
                content: '℃';
                font-size: 36px;
            }
        }
        &.pressure{
            max-width: 100px;
            margin-right: 45px;
        }
        &.draft{
            max-width: 100px;
            text-align: center;
        }
    }
}

.report{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &__column{
        &:first-child{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }
    }
    &__table{
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: $white;
        &-row{
            display: block;
            &:not(:last-child){
                border-bottom: 2px solid $darkBlueBg;
            }
        }
        &-col{
            align-items: center;
            width: 121px;
            padding: 10px;
            &:not(:last-child){
                border-right: 2px solid $darkBlueBg;
            }
        }
    }
}

.graph{
    margin-right: 20px;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    &__item{
        width: 35px;
        border-radius: 2px;
        background-color: #fff;
        &:not(:last-child){
            margin-right: 9px;
        }
        &:nth-child(1){
            height: 28px;
            background-color: #26B9FF;
        }
        &:nth-child(2){
            height: 154px;
            background-color: #2EE6FF;
        }
        &:nth-child(3){
            height: 116px;
            background-color: #26B9FF;
        }
        &:nth-child(4){
            height: 184px;
            background-color: #2D8EFF;
        }
    }
}

.workers{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &__btn-more{
        border: none;
        max-width: 100px;
        background-color: transparent;
        font-weight: 400;
        font-size: 14px;
        color: $lightBlue;
        text-align: left;
        cursor: pointer;
    }
}

.worker{
    &:not(:last-child){
        margin-bottom: 10px;
    }
    &__btn{
        width: 100%;
        background-color: transparent;
        border: none;
        display: flex;
        justify-content: space-between;
        cursor: pointer;
    }
    &__column{
        display: flex;
        &.descr{
            flex-direction: column;
            align-items: flex-start;
            font-weight: 400;
            font-size: 14px;
            line-height: 150%;
            color: $white;
        }
    }
    &__post{
        color: $lightGray;
    }
    &__tel-icon{
        margin-right: 10px;
        width: 16px;
        height: 16px;
        fill: $white;
    }
    &__message-icon{
        width: 18px;
        height: 13px;
        fill: $white;
    }
}

.millet{
    display: flex;
    flex-direction: column;
    &__row{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        &:last-child{
            margin-right: 50px;
        }
    }
    &__list{
        margin-left: 35px;
        margin-top: 21px;
        margin-right: 20px;
        margin-bottom: 20px;
        display: flex;
        flex-direction: column;
    }
    &__item{
        font-weight: 400;
        font-size: 14px;
        color: $lightGray;
        position: relative;
        &:not(:last-child){
            margin-bottom: 23px;
        }
        &:first-child::after{
            position: absolute;
            top: 0;
            left: -35px;
            content: '';
            width: 14px;
            height: 14px;
            border-radius: 5px;
            background: $blueGradient;
        }
        &:nth-child(2)::after{
            position: absolute;
            top: 0;
            left: -35px;
            content: '';
            width: 14px;
            height: 14px;
            border-radius: 5px;
            background: $purpleGradient;
        }
    }
    &__name{
        font-weight: 600;
        color: $white;
        margin-bottom: 8px;
    }
    &__ellipse{
        position: relative;
        width: 206px;
        height: 206px;
        border-radius: 103px;
        background: $purpleGradient;
        &::after{
            position: absolute;
            top: 45px;
            right: 50px;
            content: '25%';
            z-index: 2;
            font-weight: 700;
            font-size: 20px;
            letter-spacing: 1px;
            color: $white;
        }
        &::before{
            position: absolute;
            bottom: 45px;
            left: 50px;
            content: '75%';
            z-index: 2;
            font-weight: 700;
            font-size: 20px;
            letter-spacing: 1px;
            color: $white;
        }
        &-part{
            position: absolute;
            top: 0;
            left: 30px;
            width: 176px;
            fill: $lightBlue;
        }
    }
}

.technic{
    &__row{
        height: 100%;
        display: flex;
        flex-wrap: wrap;
    }
    &__col{
        position: relative;
        overflow: hidden;
        height: 100%;
        display: flex;
        flex-direction: column;
    }
    &__name-company{
        margin-top: 19px;
        font-weight: 400;
        font-size: 14px;
        color: $white;
    }
    &__item{
        margin-top: 19px;
        margin-left: 40px;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: $lightGray;
        position: relative;
        &::after{
            position: absolute;
            top: 3px;
            left: -35px;
            content: '';
            width: 14px;
            height: 14px;
            border-radius: 5px;
            background: $blueGradient;
        }
    }
    &__name{
        font-weight: 700;
        color: $white;
    }
    &__btn-more{
        margin-top: 39px;
        border: none;
        background-color: transparent;
        font-weight: 400;
        font-size: 14px;
        color: $lightBlue;
        text-align: left;
        cursor: pointer;
    }
    &__table{
        border: none;
        margin-left: 13px;
        display: flex;
        flex-direction: column;
        &-row{
            display: flex;
            &:first-child{
                position: relative;
                overflow: hidden;
                height: 170px;
            }
            &:last-child{
                height: 16px;
                border-top: 2px solid $darkBlueBg;
                font-weight: 400;
                font-size: 14;
                text-align: center;
                color: $lightGray;
            }
        }
        &-col{
            width: 142px;
            border-right: 2px solid $darkBlueBg;
            &:nth-child(3n + 1){
                position: relative;
                z-index: 10;
                background-color: $blueCardBg;
                width: 13px;
            }
        }
    }
    &__grapf{
        position: absolute;
        top: -0px;
        left: -383px;
        width: 1055px;
        height: 200px;
        fill: rgba(0,0,0,.1);
        stroke: $lightBlue;
    }
}


.section{
    &__header{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        margin: 0 10px;
    }
    &__name{
        font-weight: 600;
        color: $white;
        font-size: 20px;
        margin-bottom: 15px;
        margin-right: 23px;
        &--blue{
            margin-left: 17px;
            font-weight: 400;
            font-size: 16px;
            color: $lightBlue;
        }
    }
    &__btns{
        display: flex;
        align-items: center;
        max-width: 700px;
        width: 100%;
    }
    &__inp{
        max-width: 465px;
        min-width: 200px;
        width: 100%;
        margin-right: 23px;
        padding: 8px 16px;
        background-color: #3E4459;
        border: 1px solid transparent;
        border-radius: 5px;
        font-weight: 300;
        font-size: 14px;
        color: #C4C4C4;
        transition: border, color .3s ease 0s;
        &:focus{
            outline: none;
            border-color: $lightBlue;
            color: $white;
        }
    }
    &__btn{
        background-color: transparent;
        border: none;
        cursor: pointer;
        fill: $white;
        stroke: $white;
        &:not(:last-child){
            margin-right: 23px;
        }
        &:hover{
            fill: $lightBlue;
            stroke: $lightBlue;
        }
    }
    &__material{
        margin-right: 23px;
        padding: 7px 30px;
        border: none;
        border-radius: 6px;
        font-weight: 600;
        font-size: 16px;
        line-height: 150%;
        color: $darkBlue;
        cursor: pointer;
        transition: background-color .3s ease 0s;
        &:hover{
            background-color: $lightBlue;
        }
    }
    &__add{
        width: 28px;
        height: 28px;
        fill: inherit;
        transition: fill .3s ease 0s;
    }
    &__settings{
        width: 28px;
        height: 28px;
        stroke: inherit;
        transition: stroke .3s ease 0s;
    }
}

@media (max-width: (1090px)) {
    .home{
        margin-left: 35px;
    }
    .home__inner{
        padding-left: 10px;
        padding-right: 10px;
    }
}
@media (max-width: (780px)) {
    .home__content{
        flex-direction: column;
        margin-left: 0;
        margin-right: 0;
    }
    .home__item{
        margin: 5px;
    }
}
@media (max-width: (450px)) {
    .section__name--blue{
        margin: 0;
    }
    .section__material{
        margin-right: 10px;
    }
    .section__btn:first-child{
        margin-right: 10px;
    }
}
