.org{
    width: 100%;
    height: 100%;
    background-color: $blueBg;
    &__inner{
        padding: 55px 79px 55px 37px;
    }
    &__content{
        margin-top: 58px;
        border-radius: 18px;
        background-color: $blueCardBg;
        padding: 17px 19px;
        max-height: 770px;
        overflow-y: scroll;
    }
    &__table{
        width: 100%;
        &-row{
            display: flex;
            & + .org__table-row{
                border-top: 1px solid $darkBlue;
            }
            &.title{
                & > td{
                    font-weight: 600;
                    color: $white;
                }
        }
        }
        &-col{
            width: 100%;
            padding: 16px 0;
            font-weight: 400;
            color: $lightGray;
            font-size: 12px;
            line-height: 150%;
            & + .org__table-col{
                border-left: 1px solid $darkBlue;
            }
            &:not(:first-child){
                padding-left: 28px;
            }
            &.name{
                max-width: 226px;
                min-width: 175px;
                flex-shrink: 2;
            }
            &.inn{
                max-width: 128px;
                min-width: 100px;
                flex-shrink: 2;
            }
            &.director{
                max-width: 242px;
                min-width: 170px;
                flex-shrink: 2;
            }
            &.addres{
                max-width: 401px;
            }
            &.icon{
                padding-left: 11px;
                width: 50px;
            }
        }
    }
    &__btn{
        background-color: transparent;
        border: none;
        cursor: pointer;
        fill: $white;
        transition: fill .3s ease 0s;
        &-icon{
            width: 15px;
            height: 14px;
            fill: inherit;
        }
        &:hover{
            fill: $lightBlue;
        }
    }
}

@media (max-width: 1300px){
    .org__content{
        margin-top: 20px;
    }
    .org__inner{
        padding-left: 10px;
        padding-right: 10px;
        padding-top: 20px;
    }
    .org__table-col.icon{
        width: 70px;
    }
}
@media (max-width: 900px){
    .org{
        margin-left: 35px;
    }
    .org__content{
        padding: 5px;
        overflow-x: scroll;
    }
    .org__table-col{
        padding: 5px;
    }
    .org__table-col.name{
        min-width: 120px;
    }

}
