.nav-bar{
    position: relative;
    top: 0;
    left: 0;
    z-index: 15;
    height: 100%;
    max-width: 241px;
    width: 100%;
    background-color: $darkBlueBg;
    padding: 31px 10px;
    transition: max-width .3s ease 0s;
}
.nav{
    display: flex;
    flex-direction: column;
    &__list{
        display: flex;
        flex-direction: column;
    }
    &__item{
        &:not(:last-child){
            margin-bottom: 1px;
        }
        &:last-child{
            margin-top: 70px;
        }
    }
    &__icon{
        flex: 0 0 auto;
        display: block;
        margin-right: 10px;
        width: 20px;
        height: 18px;
        fill: #fff;
        transition: fill .3s ease 0s;
    }
    &__link{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 12px 6px;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: $white;
        transition: color .3s ease 0s;
        &:hover{
            color: $lightBlue;
            & > .nav__icon{
                fill: $lightBlue;
            }
        }
        &-value{
            opacity: 0;
        }
    }
}

.subnav{
    &__list{
        display: flex;
        flex-direction: column;
    }
    &__item{
        width: 100%;
        &:not(:last-child){
            margin-bottom: 1px;
        }
    }
    &__link{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 11px 6px;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: $white;
        border-radius: 10px;
        transition: color, background-color .3s ease 0s;
        &:hover{
            color: $lightBlue;
            & > .subnav__icon{
                fill: $lightBlue;
            }
        }
        &.active{
            background: $blueGradient;
            color: $darkBlueBg;
            & > .subnav__icon{
                fill: $darkBlueBg;
            }
        }
    }
    &__icon{
        flex: 0 0 auto;
        display: block;
        margin-right: 10px;
        width: 24px;
        height: 24px;
        fill: #fff;
        transition: fill .3s ease 0s;
    }
}

@media (max-width: (900px)) {
    .nav-bar{
        position: fixed;
        padding-left: 0;
        max-width: 36px;
        overflow: hidden;
        & > .nav__link--value{
            opacity: 1;
        }
        &:hover{
            max-width: 241px;
        }
    }
}